	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"busbud.com\">Busbud.com</a> is a intercity Bus OTA that sells bus tickets online in 63 countries on a million + routes. As part of the on-going effort of improving the website experience, I redesigned the core funel on mobile web and desktop.</p>\n<h1 class=\"w-100 text-center my-7\">\n  A design system isn&apos;t about looks, it about solving the problems you commonly run into in a systematic, intuitive and consistant manner.\n</h1>\n<p>In busbud case: address a couple of creeping UX &#x26; technical issues that busbud was facing.</p>\n<ol>\n<li>Improve the mobile experience, conversation rates and satisfaction.</li>\n<li>Revisit the website navigation to better align with users intention to navigate between pages of the funel.</li>\n<li>Implement a solid component based design system to increase consistancy, speed up the design process and front-end development.</li>\n<li>Create a design system that would play nice with our future development goals.</li>\n</ol>\n<h2>Design</h2>","frontmatter":{"date":"2017-09-01T12:00:00Z","path":"/busbud-redesign","title":"Busbud Redesign","timeframe":"March - June 2017","images":{"first":"bb1","second":"bb2"},"gradient":{"left":"#5CAFEC","right":"#5CAFEC"}}}},"pathContext":{}}

